import {
  Box,
  createTheme,
  ThemeProvider,
  useMediaQuery
} from '@mui/material';
import React, { useEffect, useState } from 'react';
import { Navigate, Route, BrowserRouter as Router, Routes, useLocation, useNavigate } from 'react-router-dom';
import { Navigation } from './components/Navigation';
import { User } from './model';
import { Assistant } from './pages/Assistant';
import { Home } from './pages/Home';
import { Relations } from './pages/Relations';
import { Taxes } from './pages/Taxes';
import { Wealth } from './pages/Wealth';
import users from './users.json';
import { Button, Typography } from '@mui/material';

const theme = createTheme({
  palette: {
    primary: {
      main: '#FF0402',
    },
    secondary: {
      main: '#FF8C00', // Changed to a warm orange that complements the primary red
    },
    background: {
      default: '#F9FAFB',
      paper: '#FFFFFF',
    },
    text: {
      primary: '#111827',
      secondary: '#6B7280',
    },
  },
  typography: {
    fontFamily: 'SF Pro Display, Arial, sans-serif',
  },
  components: {
    MuiCard: {
      styleOverrides: {
        root: {
          boxShadow: 'none',
          border: '1px solid #E5E7EB',
          borderRadius: 4,
          padding: 16,
        },
      },
    },
    MuiButton: {
      styleOverrides: {
        root: {
          borderRadius: 4,
          textTransform: 'none',
          fontWeight: 600,
          boxShadow: 'none',
        },
      },
    },
    MuiTextField: {
      styleOverrides: {
        root: {
          '& .MuiOutlinedInput-root': {
            borderRadius: 4,
          },
        },
      },
    },
    MuiAvatar: {
      styleOverrides: {
        root: {
          borderRadius: 4,
        },
      },
    },
    MuiPaper: {
      styleOverrides: {
        root: {
          boxShadow: 'none',
        },
      },
    },
  },
});

const Intro: React.FC = () => {
  const navigate = useNavigate();

  return (
    <Box sx={{ 
      display: 'flex', 
      flexDirection: 'column', 
      alignItems: 'center', 
      justifyContent: 'center', 
      height: '100vh',
      p: 3
    }}>
      <Typography variant="h2" component="h1" sx={{ color: '#FF0402', mb: 4 }}>
        Your Taxes
      </Typography>
      <Box sx={{ display: 'flex', gap: 2 }}>
        <Button variant="contained" color="primary" onClick={() => navigate('/home')}>
          Login
        </Button>
        <Button variant="outlined" color="primary">
          Register
        </Button>
      </Box>
    </Box>
  );
};

const AppContent: React.FC = () => {
  const isDesktop = useMediaQuery(theme.breakpoints.up('md'));
  //@ts-ignore
  const [user, setUser] = useState<User>(users);
  const navigate = useNavigate();
  const location = useLocation();

  useEffect(() => {
    console.log(`Route changed to: ${location.pathname}`);
  }, [location]);

  return (
    <Box sx={{ display: 'flex', bgcolor: 'background.default', minHeight: '100vh' }}>
      {isDesktop && location.pathname !== '/' && <Navigation />}
      <Box sx={{ 
        flexGrow: 1, 
        pb: { xs: 7, md: 0 } // Add padding bottom on mobile
      }}>
        <Routes>
          <Route path="/" element={<Intro />} />
          <Route path="/home" element={<Home user={user}/>} />
          <Route path="/assistant" element={<Assistant user={user}/>} />
          <Route path="/relations" element={<Relations user={user} />} />
          <Route path="/wealth" element={<Wealth user={user}/>} />
          <Route path="/taxes" element={<Taxes user={user}/>} />
          <Route path="*" element={<Navigate to="/" replace />} />
        </Routes>
      </Box>
      {!isDesktop && location.pathname !== '/' && <Navigation />}
    </Box>
  );
};

export const App: React.FC = () => {
  return (
    <ThemeProvider theme={theme}>
      <Router>
        <AppContent />
      </Router>
    </ThemeProvider>
  );
};