import SearchIcon from '@mui/icons-material/Search';
import {
  Box,
  Card,
  Collapse,
  Divider,
  Grid,
  InputAdornment,
  MenuItem,
  Table,
  TableBody,
  TableCell,
  TableContainer,
  TableRow,
  TextField,
  Tooltip,
  Typography
} from '@mui/material';
import { useState } from 'react';
import { Taxable, TaxableSubType, TaxableSubTypeDescriptions, TaxableType, User } from '../model';

export const Taxes = ({ user }: { user: User }) => {
  const [searchTerm, setSearchTerm] = useState('');
  const [selectedYear, setSelectedYear] = useState(2022);

  const years = [2022, 2021, 2020]; // Add more years as needed

  const handleYearChange = (event: React.ChangeEvent<HTMLInputElement>) => {
    setSelectedYear(Number(event.target.value));
  };


  const getTaxables = () => { 
    return user?.taxables?.filter(t => t.taxYear === selectedYear) || [];
  }

  const filterTaxable = (type: TaxableType) => {
    return getTaxables().filter(t => t.type === type) || [];
  }

  const sumTaxable = (type: TaxableType) => {
    return getTaxables().filter(t => t.type === type).reduce((acc, t) => acc + t.amountNet, 0) || 0;
  }

  const filteredTaxables = getTaxables().filter(t =>
    TaxableSubTypeDescriptions[t.subType].toLowerCase().includes(searchTerm.toLowerCase()) ||
    t.other?.fullName.toLowerCase().includes(searchTerm.toLowerCase())
  ) || [];

  return (
    <Box sx={{ p: 2, bgcolor: 'background.default' }}>
      <Box sx={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center', mb: 2 }}>
        <Typography variant="h4" fontWeight="bold">
          <Box component="span" sx={{ color: 'primary.main' }}>You</Box> Taxes
        </Typography>
        <Box sx={{ display: 'flex', alignItems: 'center' }}>
          <Typography variant="body1" sx={{ mr: 2 }} color="text.secondary">Fiscal Year</Typography>
          <TextField
            select
            value={selectedYear}
            onChange={handleYearChange}
            variant="outlined"
            size="small"
            sx={{
              minWidth: 100,
              '& .MuiOutlinedInput-root': {
                '& fieldset': { borderColor: '#E5E7EB' },
                '&:hover fieldset': { borderColor: '#E5E7EB' },
                '&.Mui-focused fieldset': { borderColor: 'primary.main' },
              },
            }}
          >
            {years.map((year) => (
              <MenuItem key={year} value={year}>
                {year}
              </MenuItem>
            ))}
          </TextField>
        </Box>
      </Box>

      <Grid container spacing={2}>
        <Grid item xs={12} md={12}>
          <Card sx={{ height: '100%', bgcolor: 'background.paper', p: 2 }}>
            <Typography variant="h6" sx={{ mb: 2 }}>Summary</Typography>
            <Box sx={{ mb: 2 }}>
              <Typography variant="body2" color="text.secondary">Taxable Income</Typography>
              <Typography variant="h5">{sumTaxable(TaxableType.INCOME).toLocaleString('de-CH', { style: 'currency', currency: 'CHF' })}</Typography>
            </Box>
            <Box sx={{ mb: 2 }}>
              <Typography variant="body2" color="text.secondary">Taxable Wealth</Typography>
              <Typography variant="h5">{sumTaxable(TaxableType.WEALTH).toLocaleString('de-CH', { style: 'currency', currency: 'CHF' })}</Typography>
            </Box>

            <Box sx={{ mb: 2 }}>
              <Typography variant="body2" color="text.secondary">Total Tax Due</Typography>
              <Typography variant="h5" color="primary.main">XX'XXX CHF</Typography>
            </Box>
            <Divider sx={{ my: 2, borderBottomWidth: '1px' }} />
            <Typography variant="subtitle2" sx={{ mb: 1 }}>Tax Breakdown</Typography>
            <Box sx={{ display: 'flex', justifyContent: 'space-between', mb: 1 }}>
              <Typography variant="body2" color="text.secondary">Federal (Bund)</Typography>
              <Typography variant="body2">5'620 CHF</Typography>
            </Box>
            <Box sx={{ display: 'flex', justifyContent: 'space-between', mb: 1 }}>
              <Typography variant="body2" color="text.secondary">Cantonal (Kanton)</Typography>
              <Typography variant="body2">8'430 CHF</Typography>
            </Box>
            <Box sx={{ display: 'flex', justifyContent: 'space-between' }}>
              <Typography variant="body2" color="text.secondary">Municipal (Gemeinde)</Typography>
              <Typography variant="body2">4'685 CHF</Typography>
            </Box>
          </Card>
        </Grid>
        <Grid item xs={12} md={12} spacing={4}>
          <TextField
            fullWidth
            variant="outlined"
            value={searchTerm}
            onChange={(e) => setSearchTerm(e.target.value)}
            InputProps={{
              startAdornment: (
                <InputAdornment position="start">
                  <SearchIcon color="action" />
                </InputAdornment>
              ),
            }}
            sx={{
              bgcolor: 'background.paper',
              '& .MuiOutlinedInput-root': {
                '& fieldset': { borderColor: '#E5E7EB' },
                '&:hover fieldset': { borderColor: '#E5E7EB' },
                '&.Mui-focused fieldset': { borderColor: 'primary.main' },
              },
            }}
          />
        </Grid>
        {searchTerm ? (
          <Grid item xs={12} md={12}>
            <TaxSection
              title="Search Results"
              taxables={filteredTaxables}
              isSearchResult={true}
            />
          </Grid>
        ) : (
          <>
            <Grid item xs={12} md={12}>
              <TaxSection
                title="Income"
                taxables={filterTaxable(TaxableType.INCOME)}
              />
            </Grid>

            <Grid item xs={12} md={12}>
              <TaxSection
                title="Wealth"
                taxables={filterTaxable(TaxableType.WEALTH)}
              />
            </Grid>

            <Grid item xs={12} md={12}>
              <TaxSection
                title="Deductions"
                taxables={filterTaxable(TaxableType.DEDUCTABLE)}
              />
            </Grid>
          </>
        )}
      </Grid>
    </Box>
  );
};

const groupTaxablesBySubTypeAndSum = (taxables: Taxable[]): { subType: TaxableSubType; totalAmountNet: number }[] => {
  const groupedTaxables = taxables.reduce((acc, t) => {
    acc[t.subType] = (acc[t.subType] || 0) + t.amountNet;
    return acc;
  }, {} as { [key in TaxableSubType]?: number });

  return Object.entries(groupedTaxables)
    .map(([subType, totalAmountNet]) => ({
      subType: subType as TaxableSubType,
      totalAmountNet: totalAmountNet || 0
    }))
    .sort((a: any, b: any) => b.totalAmountNet - a.totalAmountNet);
}

const TaxSection = ({ title, taxables, isSearchResult = false }: { title: string, taxables: Taxable[], isSearchResult?: boolean }) => {
  const [expandedItems, setExpandedItems] = useState<Set<TaxableSubType>>(new Set());
  const groupedTaxables = isSearchResult ? [] : groupTaxablesBySubTypeAndSum(taxables);
  const total = taxables.reduce((sum, item) => sum + item.amountNet, 0);

  const handleExpand = (subType: TaxableSubType) => {
    setExpandedItems(prevItems => {
      const newItems = new Set(prevItems);
      if (newItems.has(subType)) {
        newItems.delete(subType);
      } else {
        newItems.add(subType);
      }
      return newItems;
    });
  };

  return (
    <Card sx={{ height: '100%', bgcolor: 'background.paper', p: 2 }}>
      <Typography variant="h6" sx={{ mb: 2 }}>{title}</Typography>
      <TableContainer>
        <Table size="small">
          <TableBody>
            {isSearchResult ? (
              taxables.map((taxable, index) => (
                <TableRow key={index} hover>
                  <TableCell component="th" scope="row" sx={{ color: 'text.secondary' }}>
                    {TaxableSubTypeDescriptions[taxable.subType]} - {taxable.other?.fullName}
                    {getStringOfTaxableReferenceForTaxable(taxable)}
                  </TableCell>
                  <TableCell align="right">
                    {taxable.amountNet.toLocaleString('de-CH', { style: 'currency', currency: "CHF" })}
                  </TableCell>
                </TableRow>
              ))
            ) : (
              groupedTaxables.map((item, index) => (
                <>
                  <TableRow
                    key={index}
                    sx={{
                      '&:last-child td, &:last-child th': { border: 0 },
                      cursor: 'pointer',
                      '&:hover': { backgroundColor: 'action.hover' }
                    }}
                    onClick={() => handleExpand(item.subType)}
                  >
                    <TableCell component="th" scope="row" sx={{ color: 'text.secondary', display: 'flex', alignItems: 'center', p :2 }}>
                      <Tooltip title={TaxableSubTypeDescriptions[item.subType]} arrow>
                        <Typography variant="body2">{TaxableSubTypeDescriptions[item.subType]}</Typography>
                      </Tooltip>
                    </TableCell>
                    <TableCell align="right">
                      <Typography variant="body2" fontWeight="bold">
                        {item.totalAmountNet.toLocaleString('de-CH', { style: 'currency', currency: "CHF" })}
                      </Typography>
                    </TableCell>
                  </TableRow>
                  <TableRow>
                    <TableCell colSpan={2} sx={{ py: 0 }}>
                      <Collapse in={expandedItems.has(item.subType)} timeout="auto" unmountOnExit>
                        <Box sx={{ my: 2 }}>
                          <TaxableDetails subType={item.subType} taxables={taxables} />
                        </Box>
                      </Collapse>
                    </TableCell>
                  </TableRow>
                </>
              ))
            )}
            <TableRow sx={{
              '& td': {
                fontWeight: 'bold',
                borderTop: '1px solid',
                borderColor: 'divider',
                py: 1.5
              }
            }}>
              <TableCell>Total</TableCell>
              <TableCell align="right">
                {total.toLocaleString('de-CH', { style: 'currency', currency: "CHF" })}
              </TableCell>
            </TableRow>
          </TableBody>
        </Table>
      </TableContainer>
    </Card>
  );
};

const getStringOfTaxableReferenceForTaxable = (taxable: Taxable) => {
  const applicable = taxable.references.filter(r => r.references.length > 0);
  if (applicable.length > 0) {
    return " - " + applicable.map(r => r.references[0].id).join(", ");
  }
  return "";
}

const TaxableDetails = ({ subType, taxables }: { subType: TaxableSubType, taxables: Taxable[] }) => {
  const filteredTaxables = taxables.filter(t => t.subType === subType);

  return (
    <Table size="small">
      <TableBody>
        {filteredTaxables.sort((a: Taxable, b: Taxable) => a.other?.fullName.localeCompare(b.other?.fullName)).map((taxable, index) => (
          <TableRow key={index} hover>
            <TableCell sx={{ borderBottom: 'none', pl: 4 }}>
              <Tooltip title={getStringOfTaxableReferenceForTaxable(taxable)} arrow>
                <Typography variant="body2">{taxable.other?.fullName}</Typography>
              </Tooltip>
            </TableCell>
            <TableCell align="right" sx={{ borderBottom: 'none', 'marginRight' : 0 }}>
              <Typography variant="body2">
                {taxable.amountNet.toLocaleString('de-CH', { style: 'currency', currency: "CHF" })}
              </Typography>
            </TableCell>
          </TableRow>
        ))}
      </TableBody>
    </Table>
  );
};