import AccountBalanceIcon from '@mui/icons-material/AccountBalance';
import AccountBalanceWalletIcon from '@mui/icons-material/AccountBalanceWallet';
import BuildIcon from '@mui/icons-material/Build';
import CreditScoreIcon from '@mui/icons-material/CreditScore';
import DirectionsTransitIcon from '@mui/icons-material/DirectionsTransit';
import HomeIcon from '@mui/icons-material/Home';
import LocalHospitalIcon from '@mui/icons-material/LocalHospital';
import SavingsIcon from '@mui/icons-material/Savings';
import SchoolIcon from '@mui/icons-material/School';
import SearchIcon from '@mui/icons-material/Search';
import ShowChartIcon from '@mui/icons-material/ShowChart';

import {
  Avatar,
  Box,
  Card,
  Grid,
  InputAdornment,
  TextField,
  Typography
} from '@mui/material';
import React from 'react';
import { TaxableSubType, User } from '../model';

export const Home = ({ user }: { user: User }) => {
  const sumTaxable = (subType: TaxableSubType) => {
    return user?.taxables.filter(t => t.subType === subType && t.taxYear === 2022).reduce((acc, t) => acc + t.amountNet, 0) || 0;
  }

  const sumTaxableByGroup = (group : string) => {
    if(group === "CASH"){
      return sumTaxable(TaxableSubType.TAXABLE_WEALTH_CASH_VALUE)
    } else if(group === "INVESTMENT"){
      return sumTaxable(TaxableSubType.TAXABLE_WEALTH_ASSET_VALUE) + 
      sumTaxable(TaxableSubType.TAXABLE_WEALTH_CRYPTO_VALUE) +
      sumTaxable(TaxableSubType.TAXABLE_WEALTH_PE_VALUE) + 
      sumTaxable(TaxableSubType.TAXABLE_WEALTH_PORTFOLIO)
    }  else {
      return 0;
    }
  }

  const Sum = () => {
    return sumTaxable(TaxableSubType.TAXABLE_WEALTH_ASSET_VALUE) + 
      sumTaxable(TaxableSubType.TAXABLE_WEALTH_CRYPTO_VALUE) +
      sumTaxable(TaxableSubType.TAXABLE_WEALTH_PE_VALUE) + 
      sumTaxable(TaxableSubType.TAXABLE_WEALTH_PORTFOLIO) + 
      sumTaxable(TaxableSubType.TAXABLE_WEALTH_CASH_VALUE)
  }

  return <Box sx={{ p: 2, bgcolor: 'background.default', minHeight: '100vh' }}>
    <Typography variant="h4" fontWeight="bold" sx={{ mb: 1 }}>Hi, <Box component="span" sx={{ color: 'primary.main' }}>You</Box> ☀️</Typography>
    <Typography variant="h5" sx={{ mb: 2 }} color="text.secondary">Your Wealth</Typography>

    <Card sx={{ mb: 2, bgcolor: 'background.paper' }}>
      <Grid container spacing={2}>
        <Grid item xs={12}>
          <Box display="flex" justifyContent="space-between" alignItems="center">
            <Box display="flex" alignItems="center">
              <Avatar sx={{ bgcolor: '#FF0402', mr: 2, width: 40, height: 40 }}>
                {React.cloneElement(<AccountBalanceIcon />, { sx: { color: '#FFFFFF' } })}
              </Avatar>
              <Typography variant="body1" fontWeight="medium">Cash</Typography>
            </Box>
            <Box textAlign="right">
              <Typography variant="body1" fontWeight="bold">{sumTaxableByGroup("CASH").toLocaleString('de-CH', { style: 'currency', currency: 'CHF' })}</Typography>
            </Box>
          </Box>
        </Grid>
        <Grid item xs={12}>
        <Grid container spacing={2}>
        <Grid item xs={12}>
          <Box display="flex" justifyContent="space-between" alignItems="center">
            <Box display="flex" alignItems="center">
              <Avatar sx={{ bgcolor: '#66BB6A', mr: 2, width: 40, height: 40 }}>
                {React.cloneElement(<ShowChartIcon />, { sx: { color: '#FFFFFF' } })}
              </Avatar>
              <Typography variant="body1" fontWeight="medium">Investments</Typography>
            </Box>
            <Box textAlign="right">
            <Typography variant="body1" fontWeight="bold">{sumTaxableByGroup("INVESTMENT").toLocaleString('de-CH', { style: 'currency', currency: 'CHF' })}</Typography>
            </Box>
          </Box>
        </Grid>
        </Grid>
        </Grid>
      </Grid>
    </Card>

    <TextField
      fullWidth
      variant="outlined"
      InputProps={{
        startAdornment: (
          <InputAdornment position="start">
            <SearchIcon color="action" />
          </InputAdornment>
        ),
      }}
      sx={{
        mb: 2,
        bgcolor: 'background.paper',
        '& .MuiOutlinedInput-root': {
          '& fieldset': { borderColor: '#E5E7EB' },
          '&:hover fieldset': { borderColor: '#E5E7EB' },
          '&.Mui-focused fieldset': { borderColor: 'primary.main' },
        },
      }}
    />

    <Box sx={{ mb: 2, display: 'flex', justifyContent: 'space-between', alignItems: 'center' }}>
      <Typography variant="h5" sx={{ mb: 2 }} color="text.secondary">Recommendations</Typography>
      {/* 
        
        <Button color="primary" endIcon={<ArrowForwardIcon />}>
          See all
        </Button>
        */}
    </Box>

    <Grid container spacing={2}>
      {[
        {
          title: "Pillar 3a Contribution",
          content: "Maximize your Pillar 3a contribution of 6,883 CHF this year to reduce your taxable income and save up to 2,200 CHF in taxes.",
          icon: <SavingsIcon />,
          chart: <ProgressChart progress={80} color="#4CAF50" />,
        },
        {
          title: "Pension Fund Buy-in",
          content: "Consider a voluntary buy-in to your pension fund. You have a potential of 50,000 CHF, which could save you approximately 15,000 CHF in taxes.",
          icon: <AccountBalanceIcon />,
          chart: <ComparisonChart current={50000} previous={0} color="#2196F3" />,
        },
        {
          title: "Charitable Donations",
          content: "Increase your charitable donations. You can deduct up to 20% of your net income, potentially saving 3,500 CHF in taxes this year.",
          icon: <CreditScoreIcon />,
          chart: <TrendChart data={[1000, 1500, 2000, 2500, 3000]} color="#9C27B0" />,
        },
        {
          title: "Home Office Deduction",
          content: "Claim home office expenses. With 2 days/week WFH, you could deduct about 1,200 CHF for the year, saving approximately 360 CHF in taxes.",
          icon: <HomeIcon />,
          chart: <ProgressChart progress={40} color="#FF9800" />,
        },
        {
          title: "Professional Development",
          content: "Deduct work-related education expenses. Your recent course fee of 2,500 CHF is fully deductible, saving about 750 CHF in taxes.",
          icon: <SchoolIcon />,
          chart: <ComparisonChart current={2500} previous={0} color="#F44336" />,
        },
        {
          title: "Property Maintenance",
          content: "Plan major home repairs strategically. Grouping 15,000 CHF of repairs in one year could lead to a tax saving of about 4,500 CHF.",
          icon: <BuildIcon />,
          chart: <TrendChart data={[3000, 3000, 15000, 3000, 3000]} color="#00BCD4" />,
        },
        {
          title: "Debt Interest Deduction",
          content: "Optimize your mortgage strategy. By not amortizing 100,000 CHF of your mortgage, you save 1,500 CHF in interest deductions annually.",
          icon: <AccountBalanceWalletIcon />,
          chart: <ComparisonChart current={100000} previous={0} color="#8BC34A" />,
        },
        {
          title: "Health Insurance Premiums",
          content: "Deduct excess health insurance premiums. Your total premiums of 8,000 CHF exceed the standard deduction by 2,500 CHF, saving about 750 CHF in taxes.",
          icon: <LocalHospitalIcon />,
          chart: <ProgressChart progress={70} color="#FF5722" />,
        },
        {
          title: "Commuting Expenses",
          content: "Claim maximum commuting expenses. Your annual public transport pass of 3,800 CHF is fully deductible, saving approximately 1,140 CHF in taxes.",
          icon: <DirectionsTransitIcon />,
          chart: <TrendChart data={[3200, 3400, 3600, 3800, 3800]} color="#795548" />,
        },
      ].map((item, index) => (
        <Grid item xs={12} sm={6} md={4} key={index}>
          <Card sx={{
            p: 2,
            height: '100%',
            display: 'flex',
            flexDirection: 'column',
            transition: 'box-shadow 0.3s',
            '&:hover': {
              boxShadow: '0 4px 20px 0 rgba(0,0,0,0.12)',
            },
          }}>
            <Box sx={{ display: 'flex', alignItems: 'center', mb: 2 }}>
              <Avatar sx={{ bgcolor: 'primary.main', color: 'white', mr: 2 }}>{item.icon}</Avatar>
              <Typography variant="subtitle1" fontWeight="bold">{item.title}</Typography>
            </Box>
            <Typography variant="body2" sx={{ mb: 2, flexGrow: 1 }}>{item.content}</Typography>
            <Box sx={{ height: 60 }}>
              {item.chart}
            </Box>
          </Card>
        </Grid>
      ))}
    </Grid>

    {/* 
      <Button 
        variant="outlined" 
        color="primary" 
        fullWidth 
        sx={{ mt: 2 }}
        endIcon={<ArrowForwardIcon />}
      >
        View All Financial Insights
      </Button>
      */}
  </Box>
}

const TrendChart = ({ data, color }) => (
  <Box sx={{ width: '100%', height: '100%', display: 'flex', alignItems: 'flex-end' }}>
    {data.map((value, index) => (
      <Box
        key={index}
        sx={{
          width: `${100 / data.length}%`,
          height: `${(value / Math.max(...data)) * 100}%`,
          bgcolor: 'action.selected',
          mx: 0.5,
          '&:last-child': {
            bgcolor: color,
          },
        }}
      />
    ))}
  </Box>
);

const ProgressChart = ({ progress, color }) => (
  <Box sx={{ width: '100%', height: '100%', display: 'flex', alignItems: 'center' }}>
    <Box sx={{ width: '100%', bgcolor: 'action.hover', height: 10, borderRadius: 5 }}>
      <Box
        sx={{
          width: `${progress}%`,
          bgcolor: color,
          height: '100%',
          borderRadius: 5,
          transition: 'width 0.5s ease-in-out',
        }}
      />
    </Box>
  </Box>
);

const ComparisonChart = ({ current, previous, color }) => (
  <Box sx={{ width: '100%', height: '100%', display: 'flex', alignItems: 'flex-end' }}>
    <Box
      sx={{
        width: '45%',
        height: `${(previous / Math.max(current, previous)) * 100}%`,
        bgcolor: 'action.hover',
        mr: 1,
      }}
    />
    <Box
      sx={{
        width: '45%',
        height: `${(current / Math.max(current, previous)) * 100}%`,
        bgcolor: color,
      }}
    />
  </Box>
);
