export class Reference {
    type : string;
    id : string;

    constructor(type : string, id : string){
        this.type = type;
        this.id = id;
    }
}

export class TaxableReference {
    objectType : string;
    references : Reference[] = [];

    constructor(objectType : string){
        this.objectType = objectType;
    }

    addReference(type :string, id : string | undefined){
        if(id && id.length > 0){
            this.references.push(new Reference(type, id));
        }
        return this;
    }
}

export class Taxable {
    type: TaxableType;
    subType : TaxableSubType;
    taxYear: number | undefined;
    currency: string | undefined;

    holdings: number | undefined;

    amountGross: number | undefined;
    amountNet: number | undefined;

    references : TaxableReference[] = [];

    self : Contact | undefined;
    other : Contact | undefined;

    constructor(
        subType: TaxableSubType, taxYear: number, currency: string, amountNet : number | undefined) {
        if(subType.toString().startsWith("INCOME")){
            this.type = TaxableType.INCOME;
            this.subType = subType;
          } else if(subType.toString().startsWith("TAXABLE_WEALTH")){
            this.type = TaxableType.WEALTH;
            this.subType = subType; 
          } else if(subType.toString().startsWith("DEDUCTABLE")){
            this.type = TaxableType.DEDUCTABLE;
            this.subType = subType;
          }
        this.taxYear = taxYear;
        this.currency = currency;
        this.amountNet = amountNet;
    }

    addTaxableReference(ref : TaxableReference){
        this.references.push(ref);
        return this;
    }

    toStr(): string {
        let result = "- ";

        if (this.amountNet !== undefined) {
            // TODO: improve this later
            result += formatCurrency(this.amountNet, this.currency || "CHF") + " ";
        }

        for(const ref of this.references){
            if(ref.references.length > 0){
                result += ref.references[0].id + " "
            }
        }

        return result;
    }
}

export enum RelationType {
    EMPLOYMENT = 'EMPLOYMENT',
    BANKING = 'BANKING',
    COMMODITY = 'COMMODITY',
    REAL_ESTATE = 'REAL_ESTATE',
    INSURANCE = 'INSURANCE',
    PENSION = 'PENSION',
    PRIVATE_EQUITY = 'PRIVATE_EQUITY',
    LOAN = 'LOAN',
    DONATION = 'DONATION',
    FAMILY = 'FAMILY'
}

export class Relation {
    type : RelationType;
    description : string | undefined;

    self : Contact | undefined;
    other : Contact | undefined;

    documents : BaseDocumentSchema[] = [];
    taxables : Taxable[] = [];

    constructor(type :RelationType, self : Contact | undefined, other : Contact | undefined){
        this.type = type;
        this.self = self;
        this.other = other;
    }

    addTaxable(taxable : Taxable){
        this.taxables.push(taxable);
        taxable.self = this.self;
        taxable.other = this.other;
        return this;
    }

}

export class User {
    relations : Relation[] = [];
    taxables : Taxable[] = [];

    contacts: Contact[] = [];
    events: UserEvent[] = [];

    findContact(contactName: string | undefined): Contact | undefined {
        if(contactName === 'Unknown'){
            return undefined;
        }
        if (!contactName || contactName.length === 0) {
            return undefined;
        }

        for (const c of this.contacts) {
            if (c.fullName.toLowerCase() === contactName.toLowerCase()) {
                return c;
            }
            for (const alternative of c.alternativeNames) {
                if (alternative.toLowerCase() === contactName.toLowerCase()) {
                    return c;
                }
            }
        }

        console.log("Didn't find contact:", contactName);
        return new Contact(contactName);
    }

    findRelation(type : RelationType, self : Contact | undefined, other : Contact | undefined){
        return this.relations.find(a => a.type === type && a.self === self && a.other === other);
    }

    toConsole(): void {
        let user : User = this;
        let totalIncome = 0;
        let totalWealth = 0;
        let totalDeductibles = 0;
    
        const taxablesByType = (type: TaxableType) => user.taxables.filter(t => t.type === type);
    
        for (const type in TaxableType) {
            if (isNaN(Number(type))) {
                const taxables : Taxable[] = taxablesByType(TaxableType[type as keyof typeof TaxableType]);
                if (taxables.length > 0) {
                    let sum = 0;
                    console.log(`***************** ${TaxableType[type as keyof typeof TaxableType]} ***********`);
                    for (const t of taxables) {
                        console.log(t.toStr());
                        sum += t.amountNet;
    
                        if (t.type.toString().includes("DEDUCTABLE") && t.amountNet!!) {
                            totalDeductibles += t.amountNet;
                        }
                        if (t.type.toString().includes("INCOME") && t.amountNet!!) {
                            totalIncome += t.amountNet;
                        }
                        if (t.type.toString().includes("TAXABLE_WEALTH_VALUE") && t.amountNet!!) {
                            totalWealth += t.amountNet;
                        }
                    }
                    console.log(`= ${formatCurrency(sum, taxables[0].currency || "CHF")}`);
                }
            }
        }
    
        console.log("\n\n\n*********** OVERVIEW *************");
        console.log("Total Income " + formatCurrency(totalIncome, "CHF"));
        console.log("Total Deductibles " + formatCurrency(totalDeductibles, "CHF"));
        console.log("Taxable Income " + formatCurrency(totalIncome - totalDeductibles, "CHF"));
        console.log("Taxable Wealth " + formatCurrency(totalWealth, "CHF"));
    }    
}